// Styles for all tag-like objects, which include both Hugo categories and Hugo tags.

.post_categories,
.post_tags {
  .post_tag.button {
    margin-top: 0.25rem;
    padding-top: 0.25rem;
    font-weight: normal;
    @include switch(color, var(--hazy-green), var(--pale-green));
    @include focus(text-decoration, underline);

    @include is-on {
      @include switch(color, var(--white), var(--white));
    }

    @include is-on {
      @include switch(border-color, currentColor, var(--white));
    }
  }
}

.post_categories .post_tag--cat.button {
  font-weight: bold;
  @include switch(color, var(--hazy-green), var(--theme));
  @include switch(background-color, var(--light), var(--translucent));
  @include switch(border-color, var(--hazy-green), var(--theme));

  @include is-on {
    @include switch(color, var(--theme), var(--white));
  }
}

// Tags in the sidebar
.tags_nav .post_tag {
  margin: 0.5rem 0.5rem 0.5rem 0;
  padding-left: 1rem;
  font-size: 0.95rem;
  @include switch(color, var(--dark-green), var(--pale-green));
  @include is-on(border-color, currentColor);
  @include focus(text-decoration, underline);

  .button_tally {
    margin: 0.5rem 0 0.5rem 0.5rem;
    padding: 0.2rem 0.5rem;
    font-size: 1rem;
    border-radius: 100%;
    background-color: darken(map-get($color, bright-green), 12%);
  }
}
